$text_color: blue;
@mixin box {

    display: block;
    width: 250px;
    height: 200px;
    background-color: red; }


h1 {
    margin-top: 10px;
    color: $text_color;
    font-size: 50px;
    @include box; }
h2 {
    color: $text_color; }

.block {
    background-color: black;
    h3 {
        color: green; }
    &_color {
        background-color: orange;
        button {
            @include box; } }
    .block_color {
        color: $text_color; } }

